.portfolio__more {
  margin: 0 auto;
}
.portfolio__more.hidden {
  display: none;
}
.portfolio__more:disabled {
  opacity: 0.2;
  cursor: initial;
}
@media (max-width: 960px) {
  .portfolio__more {
    margin: 0;
  }
}
@media (max-width: 435px) {
  .portfolio__more {
    width: 40px;
    height: 40px;
    min-width: fit-content;
    padding: 0;
    transform: rotate(90deg);
    margin: 0 10px;
  }
  .portfolio__more span {
    display: none;
  }
}
.portfolio__collapse {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 58px;
  height: 58px;
  min-width: fit-content;
  padding: 0;
  transform: rotate(-90deg);
}
.portfolio__collapse:disabled {
  opacity: 0.2;
  cursor: initial;
}
@media (max-width: 435px) {
  .portfolio__collapse {
    position: relative;
    width: 40px;
    height: 40px;
    margin: 0 10px;
  }
}
.portfolio__footer {
  align-items: center;
  justify-content: center;
}
@media (max-width: 635px) {
  .portfolio__footer {
    justify-content: space-between;
  }
}
@media (max-width: 435px) {
  .portfolio__footer {
    justify-content: center;
  }
}

.features__container {
  position: relative;
}
.features__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  max-width: 1405px;
}
@media (max-width: 768px) {
  .features__list {
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    width: 100%;
  }
}
.features__list-item {
  position: relative;
}
.features__list-item picture {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.features__list-item picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--slow-transition);
}
.features__list-item-content {
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  padding-top: 50px;
}
.features__list-item-content svg {
  width: 75%;
  max-width: 220px;
  height: 75%;
  max-height: 180px;
  margin-bottom: 25px;
  fill: var(--white);
  transition: fill var(--main-transition);
}
@media (max-width: 960px) {
  .features__list-item-content svg {
    margin-bottom: 10px;
  }
}
.features__list-item-content span {
  font-weight: 600;
  font-size: 30px;
  text-align: center;
  margin-top: auto;
  color: var(--white);
  transition: color var(--main-transition);
}
@media (max-width: 1440px) {
  .features__list-item-content span {
    font-size: 26px;
  }
}
@media (max-width: 1140px) {
  .features__list-item-content span {
    font-size: 22px;
  }
}
@media (max-width: 960px) {
  .features__list-item-content span {
    font-size: 18px;
  }
}
.features__list-item:hover img, .features__list-item:focus img {
  transform: scale(1.1);
}
.features__list-item:hover .features__list-item-content svg, .features__list-item:focus .features__list-item-content svg {
  fill: var(--primary);
}
.features__list-item:hover .features__list-item-content span, .features__list-item:focus .features__list-item-content span {
  color: var(--primary);
}
.features__btn {
  margin: 0 auto;
}

.callback {
  padding: 70px 0;
  background-color: var(--primary);
}
@media (max-width: 1440px) {
  .callback {
    padding: 60px 0;
  }
}
@media (max-width: 1140px) {
  .callback {
    padding: 50px 0;
  }
}
@media (max-width: 960px) {
  .callback {
    padding: 40px 0;
  }
}
@media (max-width: 534px) {
  .callback {
    padding: 30px 0;
  }
}
.callback__container {
  display: grid;
  grid-template-columns: minmax(500px, 575px) auto;
  grid-column-gap: 40px;
}
@media (max-width: 910px) {
  .callback__container {
    grid-template-columns: 1fr;
  }
}
.callback__form p {
  font-size: 44px;
  line-height: 1.1;
  margin-bottom: 60px;
  color: var(--white);
}
@media (max-width: 1140px) {
  .callback__form p {
    font-size: 40px;
  }
}
@media (max-width: 1140px) {
  .callback__form p {
    font-size: 36px;
  }
}
@media (max-width: 960px) {
  .callback__form p {
    font-size: 32px;
  }
}
@media (max-width: 534px) {
  .callback__form p {
    font-size: 28px;
  }
}
@media (max-width: 635px) {
  .callback__form p {
    margin-bottom: 30px;
  }
}
.callback__side {
  position: relative;
}
@media (max-width: 1140px) {
  .callback__side {
    flex-direction: column;
  }
}
@media (max-width: 910px) {
  .callback__side {
    display: none;
  }
}
.callback__side-overlay-text {
  justify-content: center;
}
@media (max-width: 1140px) {
  .callback__side-overlay-text {
    order: 2;
  }
}
.callback__side-overlay-text span {
  font-weight: 700;
  font-size: 212px;
  line-height: 80%;
  text-align: center;
  color: #40BDC5;
}
@media (max-width: 1800px) {
  .callback__side-overlay-text span {
    font-size: 168px;
  }
}
@media (max-width: 1600px) {
  .callback__side-overlay-text span {
    font-size: 132px;
  }
}
@media (max-width: 1440px) {
  .callback__side-overlay-text span {
    font-size: 106px;
  }
}
@media (max-width: 1140px) {
  .callback__side-overlay-text span {
    font-size: 60px;
  }
}
.callback__side svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 60%;
  fill: var(--white);
}
@media (max-width: 1140px) {
  .callback__side svg {
    position: static;
    width: 165px;
    height: auto;
    transform: initial;
    order: 1;
  }
}

.map {
  position: relative;
  width: 100%;
  max-width: 2400px;
  height: 500px;
  margin: 0 auto;
  background-color: var(--light);
}
@media (max-width: 960px) {
  .map {
    height: 400px;
  }
}
@media (max-width: 534px) {
  .map {
    height: 300px;
  }
}

#yandex_map {
  width: 100%;
  max-width: 2400px;
  height: 100%;
  margin: 0 auto;
}
#yandex_map .zoom-btns {
  display: flex;
  align-items: center;
  position: absolute;
  top: -440px;
  left: -75px;
}
@media (max-width: 960px) {
  #yandex_map .zoom-btns {
    top: -340px;
  }
}
@media (max-width: 534px) {
  #yandex_map .zoom-btns {
    top: -240px;
  }
}
#yandex_map .zoom-btns .zoom-btn {
  width: 40px;
  height: 40px;
  margin: 0 5px;
  background-color: var(--primary);
  cursor: pointer;
  transition: background-color var(--main-transition);
}
#yandex_map .zoom-btns .zoom-btn svg {
  fill: var(--white);
}
#yandex_map .zoom-btns .zoom-btn:hover {
  opacity: 0.8;
}
#yandex_map .zoom-btns .zoom-btn:active {
  background-color: var(--primary);
}

.main-footer {
  padding: 30px 0;
  background-color: var(--main-footer-bg);
}
.main-footer__container {
  align-items: center;
}
@media (max-width: 768px) {
  .main-footer__container {
    flex-direction: column;
  }
  .main-footer__container > a {
    margin-bottom: 40px;
  }
}